import React, { Component } from 'react';
import P5Wrapper from 'react-p5-wrapper';
import sketch from './sketch.js';
import { TwitterTimelineEmbed } from 'react-twitter-embed';


var parStyle = {
  display: "inline-block",
  textAlign: "left",
  marginLeft: "1vw",
  marginRight: "33vw",
};


class Bio extends Component {

  constructor(props) {
    super(props);
      this.state = {
      stateSketch: sketch,
    };
  }

  render() {

    return (

     <div className = "container-fluid">

      
			<h1 style = {parStyle}>
				Biography
			</h1>
			<p style = {parStyle}>
				Welcome to my personal website! I am currently a post-doctoral scientist in the <a href = "/ifishman">Ifishman</a> group at 
        the <a href = "/IGB"> Leibniz-Institute for Freshwater Ecology and Inland Fisheries</a> in Berlin, Germany. I study the consequences of both fish and fisher behaviour for fisheries management. I mainly use experiments in the field aided by acoustic telemetry and other tagging and tracking technologies. My hope is that my research can provide new insights into both basic biological and ecological concepts and applied fisheries management.
			</p>

      <div className="my-twitter">
        <TwitterTimelineEmbed
         sourceType="profile"
         screenName="MonkCT"
         options={{height: 800, width: 400}}
        />
     </div>
     
       <div className = "profiPic">
          <img src = {require('./Profi3.jpg')} border= "6px solid $sea"/>  
      </div>

    
	  </div>

    );
  }
}

export default Bio;


