export default function sketch (p) {
var count = 30;
var tail = 20;
var Crazyballs = [];

  p.setup = function () {
    p.createCanvas(p.windowWidth, p.windowHeight);
      
    for(var i = 0; i < count; i++){
      Crazyballs[i] = new Crazyball(p.random(p.width - 30), p.random(p.height - 30), 30);
    }
  };

  p.draw = function () {
    p.clear();
    for(var cx of Crazyballs) {
      cx.move();
      cx.display();
    };
  };

class Crazyball {
  constructor(r) {
    this.x = [tail];
    this.y = [tail];
    this.r = r;
    this.xspeed = p.randomGaussian()*4;
    this.yspeed = p.randomGaussian()*4;
    this.maxspeed = 5;
    this.coly = false;
    this.colx = false;

    this.x[0]= p.width/2;
    this.y[0] = p.height/2;

    for(var i = 1; i < tail; i++){
      this.x[i] = this.x[i-1];
      this.y[i] = this.y[i-1];
    };
  };

  display() {
    for (var i = 1; i < tail; i++) {
       p.line(this.x[i-1], this.y[i-1], this.x[i], this.y[i]);
       //var h = p.map(i, 1, tail, 10, 360);
       //var alp = p.map(i, 1, tail, p.sin(p.random(0,p.PI))*360, 1);
       //p.stroke(this.h, 10, 50, this.alp);
       p.stroke(50,50,50,150);
       p.strokeWeight(p.map(i, tail, 1, 1, 10));    
     };
  };

  move() {
    this.y[0] += this.yspeed;
    this.x[0] += this.xspeed;

    this.yspeed = this.yspeed+p.randomGaussian()*5;
    this.xspeed = this.xspeed+p.randomGaussian()*5;

    if(this.xspeed > this.maxspeed){
      this.xspeed = this.maxspeed;
    }
    if(this.xspeed < -this.maxspeed){
      this.xspeed = -this.maxspeed;
    }

    if(this.yspeed > this.maxspeed){
      this.yspeed = this.maxspeed;
    }
    if(this.yspeed < -this.maxspeed){
      this.yspeed = -this.maxspeed;
    }

    if (this.y[0] >= p.height || this.y[0] <= 0) {
      this.yspeed *= -1.000000000000000000000000000002;
    }
    if (this.x[0] >= p.width || this.x[0] <= 0) {
      this.xspeed *= -1.000000000000000000000000000002;
    }

    function mousePressed(){
      this.x[0] = this.mouseX + p.randomGaussian()*p.width*0.02;
      this.y[0] = this.mouseY+ p.randomGaussian()*p.width*0.02;
    }

    for (var i = tail-1; i > 0; i--) {
        this.x[i] = this.x[i-1];
        this.y[i] = this.y[i-1];
    };

  };

  intersects(other) {
    var d = p.dist(this.x, this.y, other.x, other.y);
    if (d < this.r + other.r) {
      this.z = p.random(1);
      this.h = p.random(1);
    }
  };
};
};