export default function sketch (p) {
  var t = 0;
  p.setup = function () {
    p.createCanvas(200, 200);
  };   

p.draw = function(){
	p.clear();
	p.frameRate(30);
	for(var i=16;i--;){
		p.ellipse(100+50*p.sin(t + i*.1),100+5*p.cos(t + i*.1),25*p.sin(i*.1)+2.5,80*p.sin(-i*.5)+80);
		p.stroke(0);
		p.noFill();
	};
	if(t > 2000) t = 0;
	t +=0.01;
  };
};