import React, { Component } from 'react';
import {
	Link
} from 'react-router-dom';
import { Fade } from 'react-slideshow-image';
import mylogo from './mylogo.svg';
import twitterlogo from './Twitter_Social_Icon_Circle_White.svg';
import rglogo from './RGlogo.svg';
import linkdnlogo from './linkdnlogo.svg';

const fadeImages = [
   require('./slide2.jpg'),
   require('./slide3.jpg'),
   require('./slide4.jpg'),
   require('./slide6.jpg')
]

const fadeProperties = {
    duration: 5000,
    transitionDuration: 500,
    infinite: true,
    indicators: false,
    arrows: false
}

class Header extends Component {
  	
 
  render() {

    return (
   		
      <header>
      <p> Christopher Monk </p>
	 <nav>
	   <ul>
	    <li>
			<Link to="/">Bio</Link>
		</li>
		<li>
			<Link to="/CV">CV</Link>
		</li>
	    <li>
			<Link to="/Publications">Publications</Link>
		</li>
		<li>
			<Link to="/Links">Links</Link>
		</li>
		
	   </ul>
	 </nav> 

	 <div className = "socialLogos">
	 	<ul>
	 	 <li>
	 	 <Link to="/myTwitter">
			<img src={twitterlogo}/>
			</Link>	 
	 	 </li>
	 	<li>
	 	<Link to="/myRG">
			<img src={rglogo}/>
			</Link>	 
	 	 </li>
	 	 <li>
	 	 <Link to="/myLinkedn">
			<img src={linkdnlogo}/>
			</Link>  
	 	 </li>
	 	</ul>
	 </div>

	  <img src={mylogo} className = "logo"/>
	  <Fade {...fadeProperties}>
      <div className="each-fade">
        <div className="image-container">
          <img src={fadeImages[0]} height = "200px" width = "100%" left = "0vh"/>
        </div>
        
      </div>
      <div className="each-fade">
        <div className="image-container">
          <img src={fadeImages[1]} height = "200px" width = "100%" left = "0vh" />
        </div>
        
      </div>
      <div className="each-fade">
        <div className="image-container">
          <img src={fadeImages[2]} height = "200px" width = "100%" left = "0vh" />
        </div>
        
      </div>
      <div className="each-fade">
        <div className="image-container">
          <img src={fadeImages[3]} height = "200px" width = "100%" left = "0vh" />
        </div>
        
      </div>
    </Fade>
	  </header>
    );
  }
}

export default Header;
