import React, { Component } from 'react';
import{
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom';

// components
import Header from './components/header/header';
import Footer from './components/footer/footer';
import Bio from './components/pages/homepage';
import CV from './components/pages/CV';
import Links from './components/pages/Links';
import Publications from './components/pages/Publications';

//includes
import './Assets/css/default.min.css';



class App extends Component {
  render() {
   
    return (

  <Router basename={process.env.PUBLIC_URL}>
      <div className="App">
    <Header/>
      <Route exact path='/'component={Bio} />
      <Route exact path='/CV' component={CV} />
      <Route exact path='/Publications' component={Publications} />
      <Route exact path='/Links' component={Links} />
      <Route path='/ifishman' component={() => window.location = 'https://www.ifishman.de'}/>
      <Route path='/IGB' component={() => window.location = 'https://www.igb-berlin.de'}/>
      <Route path='/myLinkedn' component={() => window.location = 'https://www.linkedin.com/in/christopher-monk-974539137/'}/>
      <Route path='/myRG' component={() => window.location = 'https://www.researchgate.net/profile/Christopher_Monk'}/>
      <Route path='/myTwitter' component={() => window.location = 'https://twitter.com/MonkCT'}/>
      <Route path='/scholar' component={() => window.location = 'https://scholar.google.com/citations?user=JOc_-iQAAAAJ&hl=en'}/>
     <Footer/> 
      </div>
    </Router>
    );
  }
}

export default App;
